.itemContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 140px;
    height: 80px;
    background-color: rgba(255,255,255,0.25);
    overflow: hidden;
    border-radius: 7px;
    cursor: pointer;
    box-sizing: border-box;
    margin-right: 5px;
    margin-left: 5px;
    /* transition: border 100ms ease-in-out; */
}
.itemContainer:hover{
    border: 3px solid #C583B2;
}
.item{
    width: 120px;
}
.itemDisplay{
    position: absolute;
    width: 350px;
}

.item0{
    transform: translateY(120px);
}
.item1{
    transform: translateY(80px);
}
.item2{
    transform: translateY(40px);
}
.item3{
    transform: translateY(-10px);
}

.cover{
    transform: translateY(60px);

}


#show{
    opacity: 1;
    transition: all 300ms ease-in-out;
    margin-left: 0px;
}
#hide{
    opacity: 0;
    margin-left: -10px;
    transition: all 300ms ease-in-out;
}


#outline{
    border:3px solid #C583B2;
}
#none{
    border: 0px;
}