.button{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 44px;
    height: 44px;
    background-color: #F0F0F0;
    border: 3px solid #868C8A;
    border-radius: 100%;
    cursor: pointer;
    user-select: none;
    transition: border 1s ease;
    margin-right: 5px;
    margin-left: 5px;
    font-family: 'sf';
}
.button:hover{
    border: 3px solid #C583B2;

}