
.sidebarwrapper{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.sidebarItem{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 90%;
    height: 100px;
    background-color: rgba(255,255,255,0.25);
    border-radius: 5px;
    text-decoration: none;
    color: rgba(255,255,255,0.25);
    margin-top: 15px;
    font-weight: bold;
    box-sizing: border-box;
    transition: border 100ms ease-in,color 100ms ease-in;

}

.img{
    width: 70%;
    height: 70%;
    object-fit: contain;
}
.sidebarTitle{
    margin-top: 10px;
    font-weight: bold;
    font-family: 'inter';
    font-size: 15px;
}