@import './styles/fonts.css';
.App{
  display: grid;
  grid-template-columns: 85% 15%;
  background-color:#B6B9B7 ;
  height: 100vh;
  overflow: hidden;
}
.previewContainer{
  display: grid;
  height: 100vh;
  grid-template-rows: 15% 60% 25%;
}

.sceneContainer{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-width: 700px;
  /* transform: scale(1); */
}

.controlsContainer{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.btnContainer{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 15px;
}

.bottomText{
  font-weight: bold;
}

.stage{
  background: url('./assets/stage.png') no-repeat;
  background-size: 90% ;
  background-position: 30px 100%;
}

.leftContainer{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 40px;
  background-color:#B6B9B7 ;
  box-sizing: border-box;
}
.text1{
  font-weight: bold;
  user-select: none;

}
.text2{
  font-size: 12px;
  user-select: none;

}

.leftRule{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-basis:'5%';
  height:100%;
  box-sizing:border-box;
  background: url('./assets/LeftRule.svg') no-repeat;
  background-size: 18% ;
  background-position: 10px 65%;
}

.RightContainer{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 80px;
  
  flex-direction: column;
  width: 70px;
  height: 20px;
  background-color:#B6B9B7 ;
  box-sizing: border-box;
}

.bottomRule{
  flex-basis: 5%;
  background: url('./assets/BottomRule.svg') no-repeat;
  background-size: 90% ;
  background-position: 30px 100%;
}


.title{
  display: block;
  font-weight: bold;
  font-size: 40px;
  color: #C583B2;
  font-family: 'segoe';
  user-select: none;
}
.subtitle{
  display: block;
  font-weight: bold;
  font-size: 16px;
  color: #707070;
  width:480px;
  font-family: 'segoe';
  user-select: none;
}







.sidebar{
  display: flex;
  justify-content: center;
  /* align-items: center; */
  border-left: 2px solid #ADB0AE;
  height: 100%;
  overflow-y: scroll;
  /* padding-block: 100px; */
}


.sidebar::-webkit-scrollbar{
  width: 7px;
}
.sidebar::-webkit-scrollbar-thumb{
  background-color: #C583B2;


}
.sidebar::-webkit-scrollbar-track{
  background-color: #E9AFD8;


}
